'use strict';

var React = require('react');
var Reflux = require('reflux');
var Store=require('../../stores/saveStore.js');
var Actions=require('../../actions/saveActions.js');
var Grid=require('react-bootstrap/lib/Grid');
var Row=require('react-bootstrap/lib/Row');
var Col=require('react-bootstrap/lib/Col');
var Panel=require('react-bootstrap/lib/Panel');
var Label=require('react-bootstrap/lib/Label');
var Tooltip=require('react-bootstrap/lib/Tooltip');
var _=require('lodash');
var PrintDialog=require('./printDialog.jsx');

module.exports  = React.createClass({displayName: "exports",

mixins: [Reflux.connect(Store,"store")],

  componentDidMount:function(){
    Actions.findMaps();    
  },

  _open:function(id){
    Actions.openMap(id);
  },

getInitialState:function(){
    return {showDownload:false}
  },

  render: function() {
    debugger;
    var mapList=this.state.store.maps || [];
    return (

      React.createElement("div", null, 
      React.createElement("div", {className: "text-search-wrapper"}, 
      React.createElement("div", {className: "search-box"}, 
      React.createElement("button", {type: "submit", className: "search-button", onClick: this._handleCLick}, 
      React.createElement("i", {className: "fa fa-search"})
      ), 
      React.createElement("input", {onKeyPress: this.handleOnkeypress, className: "keyword-search", type: "text", placeholder: i18n.t("maps.searchMap"), ref: "search_input"})
      )
      ), 
      React.createElement("h3", null, "List of saved Maps"), 
      React.createElement("ul", null, 
        
          _.map(mapList,function(m){
              return (
                  React.createElement("li", null, 
                    React.createElement(Grid, null, 
                      React.createElement(Row, null, 
                        React.createElement(Col, {md: 6}, 
                          React.createElement("div", null, 
                            React.createElement("h5", null, 
                              React.createElement(Label, {bsStyle: "warning"}, " ", m.title), 
                              React.createElement(PrintDialog, {key: m.id, id: m._id}), 
                              React.createElement("a", {href: "#"}, 
                              React.createElement("i", {className: "pull-right fa fa-folder-open-o", title: "Open", onClick: this._open.bind(this,m._id)})
                              )
                              )
                            )
                         )
                      ), 
                         
                      React.createElement(Row, null, 
                        React.createElement(Col, {md: 6}, 
                            React.createElement(Panel, null, 
                              m.description
                            )
                        )
                      )
                    )

                  ))
            }.bind(this))
            
        
      )
      )
      );
  }

});
