/*http://facebook.github.io/react/docs/component-specs.html*/
var React = require('react');
var Reflux = require('reflux');
var LegendStore=require('../../../../stores/legendStore.js');
var LegendActions=require('../../../../actions/legendActions.js');

var If=React.createClass({displayName: "If",
  render:function(){
    if (this.props.condition){
      return React.createElement("span", null, this.props.children)
    }else{
      return null;
    }
  }
});

var LegendSymbol=React.createClass({displayName: "LegendSymbol",

  _getStrokeDasharray:function(symbol){
    var lineStyle = symbol.outline? symbol.outline.style : symbol.style;
    var ret = "";
    switch(lineStyle) {
      case "esriSLSDash":
        ret = "6,3";
        break;
      case "esriSLSDashDot":
        ret = "6,3,3,3";
        break;
      case "esriSLSDashDotDot":
        ret = "6,3,3,3,3,3";
        break;
      case "esriSLSDot":
        ret = "3,3";
        break;
    }
    return ret;
  },

  _getSymbolPath:function(symbol){
    var ret = "";
    switch(symbol.type) {
      case "esriSFS":
        ret = "M-10-10 L10 0 L10 10 L-10 10 Z";
        break;
      case "esriSMS":
        switch(symbol.style) {
          case "esriSMSCross":
            ret = "M-10 0 L10 0 M0 -10 L0 10";
            break;
          case "esriSMSDiamond":
            ret = "M-10 0 L0 -10 L10 0 L0 10 Z";
            break;
          case "esriSMSSquare":
            ret = "M-10 10 L-10 -10 L10 -10 L10 10 Z";
            break;
          case "esriSMSX":
            ret = "M-10 10 L10 -10 M-10 -10 L10 10";
            break;
          case "esriSMSTriangle":
            ret = "M 0 -10 L-10 10 L10 10 Z";
            break;
        }
        break;
      case "esriSLS":
        ret = "M-15 0 L15 0"
        break;
    }
    return ret;
  },

  render:function(){
    var image = "";
    var symbol = this.props.symbol;
    var fill = "rgb("+symbol.color[0]+","+symbol.color[1]+","+symbol.color[2]+")";
    var stroke = symbol.outline? "rgb("+symbol.outline.color[0]+","+symbol.outline.color[1]+","+symbol.outline.color[2]+")" : fill;
    var strokeWidth = symbol.outline? symbol.outline.width : symbol.width;
    var strokeDasharray = this._getStrokeDasharray(symbol);
    var path = this._getSymbolPath(symbol);
    return (
      React.createElement("svg", {overflow: "hidden", width: "30", height: "30"}, 
        React.createElement("path", {
          fill: fill, 
          stroke: stroke, 
          strokeWidth: strokeWidth, 
          strokeDasharray: strokeDasharray, 
          d: path, 
          transform: "matrix(1.00000000,0.00000000,0.00000000,1.00000000,15.00000000,15.00000000)"}
        )
      )
    );
  }

});

var LayerLegends=React.createClass({displayName: "LayerLegends",

  getInitialState: function() {
    this.state = this.state || {'expanded': false};
    return this.state;
  },

  _toggleVisibility: function(){
    this.setState({'expanded': !this.state.expanded});
  },

  render:function(){
    if (this.props.visible) {
      return (React.createElement("div", null, 
                React.createElement("div", {className: "legend-group-title", onClick: this._toggleVisibility}, 
                  React.createElement("i", {className: this.state.expanded? "fa fa-sort-asc" : "fa fa-sort-desc"}), 
                  React.createElement("span", null, this.props.layerTitle)
                ), 
                React.createElement(If, {condition: this.state.expanded}, 
                  React.createElement("div", {className: "legend-group"}, 
                    React.createElement("ul", null, 
                    
                      this.props.legendGroups.map(function(legendGroup){
                        return (
                          React.createElement("li", null, 
                            React.createElement("h2", null, legendGroup.layerName), 
                            React.createElement("div", {className: "legends-list"}, 
                              React.createElement("ul", null, 
                              
                                legendGroup.legends.map(function(legend){
                                  var image = "";
                                  if (legend.imageData || (legend.symbol && legend.symbol.imageData)){
                                    var imgData = legend.imageData? legend.imageData : legend.symbol.imageData;
                                    var imgColor = legend.imageColor? legend.imageColor : "#FFFFFF";
                                    var cntType = legend.contentType? legend.contentType : legend.symbol.contentType;
                                    var src = "data:"+cntType+";base64,"+imgData;
                                    image = React.createElement("img", {src: src, style: {backgroundColor:imgColor}});
                                  } else {
                                    image = React.createElement(LegendSymbol, {symbol: legend.symbol})
                                  }
                                  return (
                                    React.createElement("li", null, 
                                      image, 
                                      legend.label
                                    )
                                  );
                                })
                              
                              )
                            )
                          )
                        );
                      })
                    
                    )
                  )
                )
              ))
    } else {
      return React.createElement("div", null);
    }
  }

});

var LegendControl  = React.createClass({displayName: "LegendControl",
    mixins: [Reflux.connect(LegendStore)],

    getInitialState: function() {
      this.state = this.state || {'shown': false};
      return this.state;
    },

    _toggleVisibility: function(){
      LegendActions.isShown(!this.state.shown);
    },

    render: function() {
      var legendContent = "";
      var buttonLabel = this.state.shown? React.createElement(Message, {message: "layers.hideLegend"}) : React.createElement(Message, {message: "layers.showLegend"})
      if (this.state.layersLegends.length>0){
        return (React.createElement("div", {className: "legends-container"}, 
                React.createElement("button", {className: "show-legends-button", onClick: this._toggleVisibility}, buttonLabel), 
                React.createElement(If, {condition: this.state.shown}, 
                  React.createElement("div", {className: "legends-content"}, 
                    
                      this.state.layersLegends.map(function(layerLegends){
                        return (React.createElement(LayerLegends, {legendGroups: layerLegends.legendGroups, layerTitle: layerLegends.layerTitle, visible: layerLegends.visible}));
                      })
                    
                  )
                )
              )
        );
      }
      return null;
    }
});

module.exports = LegendControl;
